.header {
  width: 100%;
  height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  background: #ff6b6b;
  opacity: 0.85;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu-wrap {
  width: 1200px; /* 원본과 동일한 고정 너비 */
  display: flex;
  align-items: center;
  justify-content: center; /* 왼쪽 정렬 */
}

