.menu {
  display: flex;
  align-items: center;
  /* float 제거, flexbox 사용 */
}

.menu-container {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu-container li {
  margin-left: 20px;
}

.menu-container li:first-child {
  margin-left: 0; /* 첫번째 메뉴는 왼쪽 마진 없음 */
}

.menu-container li a {
  display: block;
  padding: 0 20px;
  line-height: 70px; /* 높이 맞춤 */
  font-size: 1.3em;
  font-weight: bold;
  text-align: center;
  color: #333;
  opacity: 1;
  transition: color 0.3s ease;
}

.menu-container li a:hover,
.menu-container li.on a {
  color: #fff;
  cursor: pointer;
}
