/* 기본 레이아웃 */
#web {
  max-width: 900px;
  margin: 0 auto;
  padding: 60px 20px;
  background-color: #fff;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

#web h2 {
  font-size: 2.2rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 40px;
  color: #4a4a4a;
}

/* 탭 리스트 */
.tab {
  display: flex;
  justify-content: center;
  text-align: center;
  gap: 30px;
  margin-bottom: 40px;
  border-bottom: 2px solid #eee;
  list-style: none;
  padding: 0;
}

.tab li {
  flex: 1;
  text-align: center;
}

.tab button {
  background: none;
  border: none;
  font-size: 1.1rem;
  padding: 10px 20px;
  cursor: pointer;
  color: #777;
  font-weight: 600;
  border-bottom: 3px solid transparent;
  transition: all 0.3s ease;
}

.tab button:hover {
  color: #ae59fd;
}

.tab li.active button {
  color: #ae59fd;
  border-bottom-color: #ae59fd;
  font-weight: 700;
}

/* 탭 컨텐츠 공통 */
.tab_con {
  color: #555;
  line-height: 1.6;
}

.tab_con h2 {
  font-size: 1.8rem;
  color: #ae59fd;
  margin-bottom: 15px;
}

.tab_con p {
  margin-bottom: 20px;
  font-size: 1rem;
}

/* 탭1 이미지 스타일 */
.tab1_con img {
  max-width: 100%;
  height: auto;
  border-radius: 6px;
  box-shadow: 0 4px 8px rgb(0 0 0 / 0.1);
  margin-bottom: 15px;
}

.site {
  display: inline-block;
  background-color: #ae59fd;
  color: white;
  padding: 10px 25px;
  border-radius: 30px;
  text-decoration: none;
  font-weight: 600;
  transition: background-color 0.3s ease;
  margin-top: 10px;
}

.site:hover {
  background-color: #8a3dcc;
}

/* 탭2 구조 */

.tab2_con .info {
  margin-bottom: 30px;
  justify-content: center;
}

.tab2_con h3 {
  color: #ae59fd;
  margin-top: 10px;
  margin-bottom: 10px;
}

.tab2_con hr {
  border: none;
  border-bottom: 1px solid #ddd;
  margin-bottom: 15px;
}

.layout {
  position: relative;
  margin-bottom: 30px;
}

.slide_wrap {
  display: flex;
  align-items: center;
  gap: 10px;
}

.slide_banner {
  flex: 1;
  overflow-x: auto;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE 10+ */
}

.slide_banner::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera */
}

.slide_banner ul {
  display: flex;
  gap: 15px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.slide_banner ul li img {
  display: block;
  max-height: 140px;
  border-radius: 6px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.slide_banner ul li img:hover {
  transform: scale(1.05);
}

.prev button,
.next button {
  background: none;
  border: none;
  cursor: pointer;
}

.prev button {
  margin-right: 20px;
}

.next button {
  margin-left: 20px;
}

.prev img,
.next img {
  width: 30px;
  height: 30px;
  opacity: 0.7;
  transition: opacity 0.3s ease;
}

.prev img:hover,
.next img:hover {
  opacity: 1;
}

/* 제작 영역 텍스트 */
.main_page h2 {
  color: #ae59fd;
  font-size: 1.6rem;
  margin-bottom: 15px;
}

.main_page p {
  font-size: 1rem;
  margin-bottom: 25px;
  white-space: pre-line; /* 줄바꿈 유지 */
}

/* 이미지 갤러리 */

#webImage {
  margin-bottom: 20px;
}

.graphicImgBox {
  overflow-x: auto;
  scrollbar-width: thin;
  scrollbar-color: #ae59fd transparent;
}

.graphicImgBox::-webkit-scrollbar {
  height: 8px;
}

.graphicImgBox::-webkit-scrollbar-thumb {
  background-color: #ae59fd;
  border-radius: 4px;
}

#graphicImage {
  display: flex;
  gap: 10px;
}

#graphicImage p {
  margin: 0;
  flex-shrink: 0;
}

#graphicImage img {
  height: 120px;
  border-radius: 6px;
  box-shadow: 0 2px 6px rgb(0 0 0 / 0.1);
  /* cursor: pointer; */
  /* transition: transform 0.3s ease; */
}

/* #graphicImage img:hover {
  transform: scale(1.05);
} */

#graphicBox {
  margin-top: 15px;
}

.graphicImg ul {
  display: flex;
  gap: 12px;
  padding: 0;
  list-style: none;
  overflow-x: auto;
  scrollbar-width: thin;
  scrollbar-color: #ae59fd transparent;
  justify-content: center;
}

.graphicImg ul::-webkit-scrollbar {
  height: 6px;
}

.graphicImg ul::-webkit-scrollbar-thumb {
  background-color: #ae59fd;
  border-radius: 3px;
}

.graphicImg li {
  flex-shrink: 0;
}

.graphicImg li img {
  height: 80px;
  width: 100px;
  border-radius: 2px;
  cursor: pointer;
  transition: opacity 0.3s ease, transform 0.3s ease;
  border: 3px solid #999;
  margin-top: 15px;
}

/* .graphicImg li.thumb.first img {
  border: 2px solid #ae59fd;
} */

.graphicImg li img:hover {
  opacity: 0.8;
  transform: scale(1.05);
}

/* 탭3 스타일 */
.tab3_con {
  text-align: center;
}

.tab3_con h2 {
  color: #ae59fd;
  margin-bottom: 15px;
}

.tab3_con p {
  font-size: 1rem;
  line-height: 1.6;
}

.tab3_con a {
  color: white;
  text-decoration: nonde;
  word-break: break-word;
}

/* 반응형 */

/* 화면 768px 이하 - 탭 세로 배치 */
@media (max-width: 768px) {
  .tab {
    flex-direction: column;
    gap: 15px;
    border-bottom: none;
    align-items: center;
  }

  .tab li button {
    font-size: 1rem;
    padding: 8px 15px;
  }

  .slide_wrap {
    flex-direction: column;
  }

  .slide_banner ul {
    flex-wrap: nowrap;
  }

  #graphicImage {
    flex-wrap: nowrap;
    overflow-x: auto;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }

  .graphicImg ul {
    overflow-x: auto;
  }
}

/* 화면 480px 이하 - 글자 크기 및 패딩 조절 */
@media (max-width: 480px) {
  #web h2 {
    font-size: 1.6rem;
    margin-bottom: 30px;
  }

  .tab li button {
    font-size: 0.9rem;
    padding: 6px 12px;
  }

  .tab_con h2 {
    font-size: 1.4rem;
  }

  .main_page p,
  .tab_con p {
    font-size: 0.9rem;
  }
}

.doctorwitch-img {
  width: 1000px;
}

.bookie-img {
  width: 1000px; /* 이미지 너비 키움 */
  max-width: 100%; /* 화면이 작을 때 자동 축소 */
  display: block; /* 가운데 정렬을 위해 block 설정 */
  margin: 0 auto; /* 가운데 정렬 */
  border-radius: 8px; /* 선택: 이미지 모서리 부드럽게 */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* 선택: 그림자 */
}

.tab2_con .info p {
  text-align: center;
}

.tab1_con {
  justify-content: center;
  align-items: center;
  text-align: center;
}

.tab2_con {
  text-align: center;
}

.used-skills {
  margin-bottom: 10px;
}

#graphicImage img {
  width: 900px;
  height: auto;
}

.tab3_con .used-skills {
  margin-top: 50px;
  margin-bottom: 50px;
}
