.profile-section {
  padding: 60px 20px;
  background-color: #fafafa;
  margin-top: 50px;
}

.profile-section h2 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 40px;
}

.profile-wrap {
  max-width: 900px;
  margin: 0 auto;
}

.info {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: space-between;
}

/* 기본: 40% 너비로 2~3단 */
.profile-block {
  flex: 1 1 40%;
  min-width: 280px; /* 너무 작아지지 않도록 */
}

.profile-block strong {
  font-size: 20px;
  display: block;
  margin-bottom: 10px;
  color: #ae59fd;
}

.profile-block ul {
  list-style: none;
  padding-left: 0;
  line-height: 1.8;
}

.skills {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: baseline;
}

.skills li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  gap: 10px;
}

/* 반응형: 화면 768px 이하일 때 한 줄 레이아웃 */
@media (max-width: 768px) {
  .info {
    flex-direction: column;
    gap: 40px;
  }

  .profile-block {
    flex: 1 1 100%;
    min-width: auto;
  }

  .profile-section {
    padding: 40px 15px;
  }

  .profile-section h2 {
    font-size: 1.6rem;
  }
}

/* 아주 작은 화면용(모바일) */
@media (max-width: 400px) {
  .skills li {
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }

  .profile-section {
    padding: 30px 10px;
  }
}

img {
  width: 70px;
}

.profile-section li {
  font-size: 16px;
}

.react {
  padding-bottom: 10px;
}

.node {
  padding-bottom: 10px;
}

.profile-block ul li {
  display: flex;
  align-items: center; /* 이미지와 텍스트 수직 정렬 */
  gap: 10px; /* 아이콘과 텍스트 간격 */
  word-break: keep-all; /* 단어 단위로 줄바꿈 (한글에 적합) */
  white-space: normal; /* 줄바꿈 허용 */
  overflow-wrap: break-word; /* 너무 긴 단어는 줄바꿈 */
}

.profile-images {
  width: 32px;
}

#profile-image2 {
  width: 25px;
  margin: 4px;
  margin-top: 13px;
}

#profile-image3 {
  width: 23px;
  margin: 5px;
  margin-top: 8px;
}

#profile-image4 {
  margin-top: 8px;
}
